<template>
    <div class="taskingRecord">
        <search :data="searchData" :config="searchConfig" @search="handlerSearch"></search>
        <!-- <toolbar 
            :toolbarConfig="toolbarConfig"
            @setPrice="setPrice"
            
        ></toolbar> -->
        <my-table :data="tableData" :config="tableConfig" @pageChange="pageChange"   ></my-table>
    </div>
</template>
<script>
import {mapState} from 'vuex'
import rule from '../../assets/js/rule.js'
export default {
    data(){
        return{
            //工艺执行人列表
            userList:[],
            selectData:[],
            rules:{
                price:rule.require('number','工艺名称不能为空'),
                doEmpNo:rule.require('string','请选择工艺执行人'),
                planDate:rule.require('string','请选择计划开始时间'),
                planHours:rule.require('number','计划工时不能为空'),
                actDate:rule.require('string','请选择实际开始时间'),
            },
            searchData:{},
            searchConfig: [
                [
                    {
                        label: "内部订单号",
                        field: "insideOrder",
                        type: "input",
                        placeholder: "请输入内部订单号"
                    },
                    {
                        label: "产品信息",
                        field: "prodName",
                        type: "input",
                        placeholder: "请输入产品信息"
                    },
                    {
                        label: "任务状态",
                        field: "status",
                        type: "select",
                        placeholder: "请输入姓名",
                        childrens:[{name:'待派工',value:'0'},{name:'处理中',value:'1'},{name:'已完成',value:'2'},{name:'审核未通过',value:'3'}]
                    },
                ]
            ],
            tableData:[],
            tableConfig:{
                isHigh: false,
                isSelection: true,
                align: "center",
                isPage: true,
                pageNo: 1,
                pageSize: 20,
                total: 0,
                colConfig: [
                    {
                        fixed: false,
                        field: "insideOrder",
                        width: "",
                        isSort: false,
                        isResize: true,
                        label: "内部订单号",
                        align: "center",
                        type: "text"
                    },
                    {
                        fixed: false,
                        field: "prodName",
                        width: "",
                        isSort: false,
                        isResize: true,
                        label: "产品信息",
                        align: "center",
                        type: "text"
                    },
                    {
                        fixed: false,
                        field: "crafworkName",
                        width: "",
                        isSort: false,
                        isResize: true,
                        label: "工艺名称",
                        align: "center",
                        type: "text"
                    },
                    {
                        fixed: false,
                        field: "price",
                        width: "",
                        isSort: false,
                        isResize: true,
                        label: "工艺工价",
                        align: "center",
                        type: "text"
                    },
                    {
                        fixed: false,
                        field: "doEmpName",
                        width: "",
                        isSort: false,
                        isResize: true,
                        label: "工艺执行人",
                        align: "center",
                        type: "text"
                    },
                    {
                        fixed: false,
                        field: "amount",
                        width: "",
                        isSort: false,
                        isResize: true,
                        label: "数量",
                        align: "center",
                        type: "text"
                    },
                    {
                        fixed: false,
                        field: "planDate",
                        width: "",
                        isSort: false,
                        isResize: true,
                        label: "计划开始时间",
                        align: "center",
                        type: "text"
                    },
                    {
                        fixed: false,
                        field: "actDate",
                        width: "120",
                        isSort: false,
                        isResize: true,
                        label: "实际开始时间",
                        align: "center",
                        type: "text"
                    },
                    {
                        fixed: false,
                        field: "getTime",
                        isSort: false,
                        isResize: true,
                        label: "领派工时间",
                        align: "center",
                        type: "text"
                    },
                    {
                        fixed: false,
                        field: "crafworkStandHours",
                        width: "",
                        isSort: false,
                        isResize: true,
                        label: "标准工时",
                        align: "center",
                        type: "text"
                    },
                    {
                        fixed: false,
                        field: "planHours",
                        width: "",
                        isSort: false,
                        isResize: true,
                        label: "计划工时",
                        align: "center",
                        type: "text"
                    },
                    {
                        fixed: false,
                        field: "statusName",
                        width: "",
                        isSort: false,
                        isResize: true,
                        label: "任务状态",
                        align: "center",
                        type: "text"
                    }
                ]
            }
        }
    },
    computed:{
        ...mapState(['ports'])
    },
    methods:{
        getList(){
            this.$http.post(
                this.ports+'/taskReceiveEmpMes/list',
                {
                    pageSize:this.tableConfig.pageSize,
                    pageNo:this.tableConfig.pageNo,
                    ...this.searchData
                },
                data=>{
                    this.tableData=data.items;
                    this.tableConfig.total=data.numRows;
                },
                this
            )
        },
        handlerSearch(){
            this.tableConfig.pageNo=1
            this.tableConfig.pageSize=20
            this.getList()
        },
        setPrice(){
            this.$prompt('工艺工价', '请输入工艺工价', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                inputPattern: /^[1-9]\d*(\.\d+)?$/,
                inputErrorMessage: '请输入工艺工价'
            }).then(({ value }) => {
                var ids=[]
                for(let val of this.selectData){
                    ids.push(val.id)
                }
                this.$http.post(
                    this.ports+'/taskPondMes/setTaskPrice',
                    {taskPondIds:ids.join(','),price:value},
                    data=>{
                        this.getList()
                    },
                    this
                )
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '取消输入'
                });       
            });
        },
        // select(data){
        //     this.selectData=data
        //     if(data.length>=1){
        //         this.toolbarConfig.btnStatus=[false]
        //     }else {
        //         this.toolbarConfig.btnStatus=[true]
        //     }
        // },
        pageChange(){
            console.log('111')
            this.getList()
        }
    },
    mounted(){
        this.getList()
    }
}
</script>
<style lang="less">
    .taskingRecord{
       padding:20px;
       background:#fff;
        .my-list{
            padding:0 20px;
            .template{
                display: flex;
                .text{
                    flex: 1;
                }
                .operate{
                    width: 100px;
                    padding-left: 30px;
                    font-size: 14px;
                }
            }
        }
    }
</style>




